import React, { useState, useEffect, useRef } from 'react'
import { Calendar, Clock, ExclamationCircle, Gear, House, ListCheck, Lock, Palette, PaletteFill, People, PersonCheck, PersonCircle, Sliders2 } from 'react-bootstrap-icons';
import Modal from 'react-bootstrap/Modal';
import placeholderBusinessImage from '../../common/assets/img/businessPlaceholderIcon.jpg';
import { Camera } from 'react-bootstrap-icons';
import Button from '../../common/components/Button';
import { toast } from 'react-toastify';
import { useNavigate } from 'react-router-dom';
import { updateBusiness } from '../../business/api/business';

function SettingsModal({ showSettingsModal, setShowSettingsModal, userInfo }) {

    const [name, setName] = useState('');
    const [description, setDescription] = useState('');
    const [logo, setLogo] = useState(null);
    const [preview, setPreview] = useState(placeholderBusinessImage);
    const inputRef = useRef(null);
    const [error, setError] = useState('');
    const navigate = useNavigate();

    const [loading, setLoading] = useState(false);

    const handleClose = () => setShowSettingsModal(false);
    const handleShow = () => setShowSettingsModal(true);

    useEffect(() => {
        setName(userInfo?.business?.name);
        setDescription(userInfo?.business?.description);
        setLogo(userInfo?.business?.logo);
        setPreview(userInfo?.business?.logo ?? placeholderBusinessImage);
    }, []);

    const handleFileChange = async (event) => {
        const file = event.target.files[0];
        if (file) {
            setLogo(file);
            setPreview(URL.createObjectURL(file));
        }
    };


    const handleSubmit = async () => {
        setLoading(true);
        setError('');

        const formData = new FormData();
        formData.append('name', name);
        formData.append('description', description);

        if (logo) {
            formData.append('logo', logo);
        }

        try {
            await updateBusiness(formData);
            setName('');
            setDescription('');
            setLogo(null);
            setPreview(placeholderBusinessImage);

            handleClose();
            toast.success("Successfully updated business details!");

            window.location.reload();

        } catch (error) {
            toast.error(error.message);
        }

        setLoading(false);
    };


    return (
        <Modal show={showSettingsModal} onHide={handleClose} size="xl" centered>
            <Modal.Body className='rounded py-0' style={{ background: 'var(--color-light-gray) !important' }}>

                <div className="d-flex">
                    <div style={{ maxWidth: 200 }} className='w-100'>
                        <nav className="pe-3">

                            <ul className="list-unstyled">

                                <div className='sidebar-link-group'>
                                    <div className='pb-2 pt-3'>
                                        <span className='small bold text-secondary ps-2'>ACCOUNT</span>
                                    </div>

                                    <li className='nav-item px-2 rounded pointer'>
                                        <a className='nav-link'>
                                            <PersonCircle />
                                            <span className='ps-2 medium'>My account</span>
                                        </a>
                                    </li>

                                    <li className='nav-item px-2 rounded pointer'>
                                        <a className='nav-link'>
                                            <Sliders2 />
                                            <span className='ps-2 medium'>Preferences</span>
                                        </a>
                                    </li>

                                    <li className='nav-item px-2 rounded pointer'>
                                        <a className='nav-link'>
                                            <Lock />
                                            <span className='ps-2 medium'>Privacy</span>
                                        </a>
                                    </li>

                                </div>


                                <div className='sidebar-link-group'>
                                    <div className='pb-2'>
                                        <span className='small bold text-secondary ps-2'>BUSINESS</span>
                                    </div>

                                    <li className='nav-item px-2 rounded pointer active'>
                                        <a className='nav-link'>
                                            <Gear />
                                            <span className='ps-2 medium'>Settings</span>
                                        </a>
                                    </li>

                                    <li className='nav-item px-2 rounded pointer'>
                                        <a className='nav-link'>
                                            <Palette />
                                            <span className='ps-2 medium'>Themes</span>
                                        </a>
                                    </li>

                                    <li className='nav-item px-2 rounded pointer'>
                                        <a className='nav-link text-danger'>
                                            <ExclamationCircle />
                                            <span className='ps-2 medium'>Danger Zone</span>
                                        </a>
                                    </li>


                                </div>


                            </ul>

                        </nav>
                    </div>
                    <div className="w-100 bg-white pt-3 pb-5 px-5">
                        <p className='bold' style={{ fontSize: 17 }}>Business Settings</p>

                        {error && <p className='text-danger small'>{error}</p>} { }

                        <div
                            style={{ maxWidth: 75 }}
                            className="mt-4 position-relative profile-image"
                        >
                            <img
                                src={preview}
                                className="img-fluid rounded hover"
                                style={{ aspectRatio: 1, objectFit: "cover" }}
                                alt="Business logo"
                            />
                            <input
                                ref={inputRef}
                                onChange={handleFileChange}
                                type="file"
                                style={{ height: 0, width: 0 }}
                                className="invisible position-absolute"
                            />
                            <button
                                className="project-image-overlay"
                                onClick={() => inputRef?.current?.click()}
                            >
                                <Camera className="text-white" />
                            </button>
                        </div>

                        <label className='pb-2 mt-4'>Name</label>
                        <input type="text" className='form-control medium bg-gray-light py-3'
                            placeholder='Business name' value={name} autoComplete="new-password"
                            onChange={(e) => setName(e.target.value)} />

                        <label className='mt-4 mb-2'>Description</label>
                        <textarea type="text" className='form-control bg-gray-light py-2 medium'
                            placeholder='Business description' value={description} autoComplete="new-password"
                            onChange={(e) => setDescription(e.target.value)} style={{ height: 130 }} />

                        <div className='pt-4'>
                            <Button variant="primary" size="sm" onClick={handleSubmit} loading={loading}>Update</Button>
                        </div>


                    </div>
                </div>

            </Modal.Body>
        </Modal>
    )
}

export default SettingsModal
